<template>
  <div class="cart">
    <div class="heads">
      <div>
        <van-icon name="passed" />
        <span>100%正品保证</span>
      </div>
      <div>
        <van-icon name="passed" />
        <span>所有商品精挑细选</span>
      </div>
      <div>
        <van-icon name="passed" />
        <span>售后无忧</span>
      </div>
    </div>
    <div class="godnum" v-if="list">
      <span
        >购物数量&nbsp;<span>{{ list.length }}</span></span
      >
      <span
        class="showing"
        @click="show = false"
        v-show="show && list.length != 0"
        >管理</span
      >
      <span
        class="showing"
        @click="show = true"
        v-show="!show && list.length != 0"
        >取消</span
      >
    </div>
    <div>
      <van-checkbox-group
        v-model="result"
        ref="checkboxGroup"
        @change="changeresult"
      >
        <van-card
          v-for="item in list"
          :key="item.id"
          :price="`${item.truePrice}`"
          desc="描述信息"
          class="gwc"
        >
          <template #thumb>
            <div class="thumbimg">
              <van-checkbox :name="item.id" class="buttonest"></van-checkbox>
              <router-link :to="`/detail/${item.productInfo.id}`" tag="div">
                <img :src="item.productInfo.image" alt="" />
              </router-link>
            </div>
          </template>
          <template #num>
            <div class="values">
              <van-button
                class="buttons"
                type="info"
                :disabled="item.cart_num == 1 ? true : false"
                @click="Cartnumadd({ num: item.cart_num, id: item.id })"
                >-</van-button
              >
              <input
                v-model="item.cart_num"
                @input="Changemodel({ num: item.cart_num, id: item.id })"
              />
              <van-button
                class="buttons"
                type="info"
                @click="Cartnumbel({ num: item.cart_num, id: item.id })"
                >+</van-button
              >
            </div>
          </template>
          <template #title>
            <p class="p1">{{ item.productInfo.store_name }}</p>
          </template>
          <template #desc>
            <p class="p10">属性:&nbsp;{{ item.productInfo.attrInfo.suk }}</p>
          </template>
          <!-- <template #num>
            <div class="num">
              <div @click="cartnum(item.cart_num)">-</div>
              <div>{{ item.cart_num }}</div>
              <div @click="item.cart_num++">+</div>
            </div>
          </template> -->
        </van-card>
      </van-checkbox-group>
    </div>
    <div class="pictrue" v-show="list && list.length == 0">
      <img src="../assets/cart.png" alt="" />
    </div>
    <recommendent :showimg="showimg" :hotlist="hotlist"></recommendent>
    <van-submit-bar class="submitbares" v-show="!showimg" :price="price">
      <template #button>
        <van-button
          class="onsubmits"
          round
          type="info"
          v-show="show"
          @click.stop="onSubmit"
          >立即下单</van-button
        >
        <van-button
          class="onsubmit"
          round
          type="info"
          v-show="!show"
          @click.stop="ondeleteing"
          >删除</van-button
        >
      </template>
      <van-checkbox type="primary" v-model="checked" @click="checkAll(checked)"
        >全选</van-checkbox
      >
    </van-submit-bar>
  </div>
</template>
<script>
import Recommendent from "@/components/Recommendent";
export default {
  data() {
    return {
      list: null,
      hotlist: [],
      show: true,
      price: 0,
      showimg: false,
      checked: false,
      result: [],
      cartid: "",
      sparray: [],
    };
  },
  components: {
    Recommendent,
  },
  created() {
    this.$axios.get("/api/cart/list").then((d) => {
      this.list = d.data.data.valid;
      if (this.list.length == 0) {
        this.showimg = true;
        return;
      }
    });
    this.$axios.get("/api/product/hot?page=1&limit=20").then((d) => {
      this.hotlist = d.data.data;
    });
  },
  methods: {
    //输入商品数量
    Changemodel(event) {
      console.log(event);
      this.axios.post("/api/cart/num", {
        //post数据
        id: event.id,
        number: event.num,
      });
      this.changeresult(event.id);
    },
    //减商品数量
    Cartnumadd(event) {
      --event.num;
      let newnum = event;
      for (let i = 0; i < this.list.length; i++) {
        if (this.list[i].id == event.id) {
          --this.list[i].cart_num;
        }
      }
      this.axios.post("/api/cart/num", {
        //post数据
        id: newnum.id,
        number: newnum.num,
      });
      this.changeresult(event.id);
    },
    //加商品数量
    Cartnumbel(event) {
      ++event.num;
      let newnum = event;
      for (let i = 0; i < this.list.length; i++) {
        if (this.list[i].id == event.id) {
          ++this.list[i].cart_num;
        }
      }
      this.axios.post("/api/cart/num", {
        //post数据
        id: newnum.id,
        number: newnum.num,
      });
      this.changeresult(event.id);
    },
    changeresult(checked) {
      this.price = 0;
      checked = this.result
      //判断选中商品状态的价格
      if(checked.length != 0){
        for (let i = 0; i < this.list.length; i++) {
          for (let j = 0; j <= checked.length; j++) {
            if (checked[j] == this.list[i].id) {
              this.price += this.list[i].truePrice * this.list[i].cart_num * 100;
            }
          }
        }
      }
      // if(checked.length == this.list.length){
      //     console.log(this.$refs.checkboxGroup)
      // }
      checked = checked.join(",");
      for (let i = 0; i < this.list.length; i++) {
        this.cartid = checked;
      }
    },
    //购物车删除
    ondeleteing() {
      console.log("aaa");
      this.$axios
        .post("/api/cart/del", {
          ids: this.cartid,
        })
        .then((d) => {
          if (d.data.status == 200) {
            this.$toast({
              message: "删除购物车成功",
            });
            this.$axios.get("/api/cart/list").then((d) => {
              this.list = d.data.data.valid;
              if (this.list.length == 0) {
                this.showimg = true;
                return;
              }
              for (let i = 0; i <= this.list.length; i++) {
                this.price +=
                  this.list[i].truePrice * this.list[i].cart_num * 100;
              }
            });
          } else {
            this.$toast(d.data.msg);
          }
        });
    },
    //如果没有选中商品就弹出提示并返回，有选中就跳转
    onSubmit() {
      if (this.cartid == "") {
        this.$toast({
          message: "请选择商品",
        });
        return;
      }
      this.$router.push({
        path: "/payment",
        query: { id: this.cartid },
      });
    },
    checkAll(checked) {
      console.log(checked);
      if (checked == true) {
        this.$refs.checkboxGroup.toggleAll(true);
      } else {
        this.$refs.checkboxGroup.toggleAll(false);
      }
    },
    toggleAll() {
      this.$refs.checkboxGroup.toggleAll();
    },
    cartnum(event) {
      event--;
      console.log(event);
    },
  },
};
</script>
<style lang="less">
.cart {
  .heads {
    height: 38px;
    background-color: #f5f5f5;
    color: #8c8c8c;
    font-size: 12px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    span {
      margin-left: 5px;
    }
  }
  .submitbares {
    position: fixed;
    bottom: 50px;
    .onsubmits {
      width: 111px;
      height: 34px;
      background-color: #e93323;
      border: none;
    }
    .onsubmit {
      width: 77px;
      height: 27.5px;
      background-color: white;
      color: #999;
      border: 1px solid #999;
    }
  }
  .godnum {
    font-size: 14px;
    padding: 0 15px;
    background-color: white;
    line-height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    span {
      &:first-child {
        span {
          color: #fc4141;
        }
      }
    }
    .showing {
      line-height: 23px;
      width: 55px;
      text-align: center;
      border: 1px solid #888;
      border-radius: 3px;
    }
  }
  .gwc {
    margin-top: 10px;
    background-color: white;
    .van-card__thumb {
      width: 110px;
    }
    .thumbimg {
      width: 110px;
      display: flex;
      align-items: center;
      .buttonest {
        font-size: 10px;
      }
      img {
        width: 80px;
        height: 80px;
        margin-left: 10px;
      }
    }
    .values {
      display: flex;
      align-items: center;
      input {
        border: none;
        font-size: 14px;
        color: #333;
        background-color: #eee;
        padding: 0;
        margin: 0 3px;
        width: 31px;
        line-height: 22px;
        text-align: center;
      }
      .van-button {
        height: 22px;
        width: 30px;
      }
      .van-button--normal {
        padding: 0;
      }
      .van-button--info {
        font-size: 26px;
        background-color: #eee;
        color: #a4a4a4;
        border: none;
      }
    }
    .p1 {
      margin-top: 4px;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    .p10 {
      color: #868686;
    }
    .num {
      width: 96px;
      display: flex;
      div {
        width: 32px;
        height: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #000;
        border: 1px solid #999;
        &:first-child,
        &:nth-child(2) {
          border-right: none;
        }
      }
    }
  }
  .pictrue {
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      margin: 35px 0 25px 0;
      width: 200px;
    }
  }
}
</style>